header{
    border-bottom: 1px solid #f0f0f0;
    position: fixed;
    width: 100%;
    z-index: 1000;
    background-color: #Fff;
    top: 0;
    .navbar{
        display: none;
    }
}

.nav-menu{
    display: flex;
    font-family: 'Montserrat';
    color: #1F373D;
    .logo{
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #FFCC01;
        padding: 25px 0;
        font-weight: 600;
        font-size: 27px;
    }
    &_content{
        display: flex;
        justify-content: space-around;
        
        li{
            display: flex;
            align-items: center;
            font-size: 15px;
            font-weight: bold;
            &:hover{
                color: #FFCC01;
               
            }
        }
    }
    &_feedback{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    &__number{
        display: flex;
        justify-content: center;
        a{
            text-decoration: none;
            font-size: 17px;
            color: #1F373D;
        }
    }
    &__button{
            display: flex;
            justify-content: center;
            padding: 5px;
            background-color: #FFCC01 ;
            border-radius: 25px;
        a{
            text-decoration: none;
            font-size: 17px;
            color: #1F373D;
        }
    }
}
