footer{
    background-color: #1F373D;
}
.footer{
    display: flex;
    justify-content: space-between;
    font-family: 'Montserrat';
    padding-top: 3%;
    padding-bottom: 3%;
    a{
        color: #919EB1;
    }
    h3{
        color: #fff;
        margin-bottom: 3%;
        font-size: 23px;
    }
    &_cards{
        display: flex;
        &__card{
            color: #919EB1;
            ul{
                list-style: none;
                padding: 0;
                margin: 0;
            }
        }
        &__card:first-child{
            margin-right: 5%;
        }
    }
    &_contacts{
        display: flex;
        flex-direction: column;
        color: #fff;
        justify-content: space-between;
        font-size: 15px;
        &__copywriting{
            display: flex;
            i{
                display: flex;
                align-items: center;
                margin-right: 1%;
            }
        }
        &__icons{
            font-size: 30px;
        }
    }
}