.contacts{
    padding-top: 7%;
    padding-bottom: 7%;
    background-color: #F6F5F4;
    font-family: 'Montserrat';
    color: #1F373D;
    &-wrapper{
        display: flex;
        justify-content: space-between;
        h3{
            font-size: 27px;
            margin-bottom: 1%;
        }
        p{
            font-size: 17px;
            margin-bottom: 1%;
        }
    }
    &_contact{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    
        &__icons{
            font-size: 40px;
        }
    }
    
}